import React from "react";
import RadarChart from "../../Charts/RadarChart";
import BarChart from "../../Charts/BarChart";
import MetricsTable from "./MetricsTable";
import ValueLabel from "./ValueLabel";

export default function Accuracy() {
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "space-between"
      }}
    >
      <div className="main-card">
        <div style={{ marginTop: "36px", textAlign: "center" }}>
          <h1>AGGREGATE ACCURACY</h1>
          <h1
            style={{
              fontSize: "4rem",
              fontFamily: "Roboto",
              fontWeight: "300",
              marginBottom: "-20px",
              color: "#5366ac"
            }}
          >
            63.7%
          </h1>
          <h4>Overall Classifier Accuracy</h4>
          <hr style={{ margin: "40px" }} />
          <ValueLabel
            value={3918}
            label="Total Tokens Classified"
            color="#5366ac"
          />
          <hr style={{ margin: "40px" }} />
          <div
            style={{
              marginLeft: "25%",
              width: "50%",
              display: "flex",
              justifyContent: "space-between"
            }}
          >
            <ValueLabel value={192} label="True Positives" color="#006f3c" />
            <ValueLabel value={2401} label="True Negatives" color="#006f3c" />
          </div>
          <div
            style={{
              marginLeft: "25%",
              width: "50%",
              display: "flex",
              justifyContent: "space-between"
            }}
          >
            <ValueLabel value={201} label="False Positives" color="#bf212f" />
            <ValueLabel value={1124} label="False Negatives" color="#bf212f" />
          </div>
          <hr style={{ margin: "40px" }} />

          <div
            style={{
              marginLeft: "10%",
              width: "80%",
              display: "flex",
              justifyContent: "space-between"
            }}
          >
            <ValueLabel value={0.83} label="Precision" color="grey" />
            <ValueLabel value={0.64} label="Recall" color="grey" />
            <ValueLabel value={0.78} label="F1" color="grey" />
            <ValueLabel value={3918} label="Support" color="grey" />
          </div>
        </div>
      </div>
      <div className="main-card">
        <div style={{ marginTop: "36px", textAlign: "center" }}>
          <h1>CATEGORY BREAKDOWN</h1>
          <h4>Figures in Percent</h4>

          <div className="chart-container-tall">
            <BarChart />
          </div>
        </div>
      </div>
      <div className="main-card">
        <div style={{ marginTop: "36px", textAlign: "center" }}>
          <h1>CLASSIFIER METRICS</h1>
        </div>
        <MetricsTable />
      </div>
    </div>
  );
}
