import React from "react";
import { Table, Button, Tag, Divider } from "antd";

const statusColours = {
  unprocessed: "red",
  processing: "orange",
  processed: "blue",
  reviewed: "green"
};

const categories = [
  "IT Infrastructure",
  "Telecom Services",
  "Operations",
  "Human Resources"
];

const dates = [
  "Jan",
  "Feb",
  "Mar",
  "Apr",
  "May",
  "Jun",
  "Jul",
  "Aug",
  "Sep",
  "Oct",
  "Nov",
  "Dec"
];

const data = [
  {
    filename: "starhub_1",
    date: "10 Sep 2019",
    accuracy: 13,
    category: "IT Infrastructure",
    status: "unprocessed",
    country: "Singapore"
  },
  {
    filename: "starhub_2",
    date: "10 Oct 2019",
    accuracy: 32,
    category: "Telecom Services",
    status: "processing",
    country: "Singapore"
  },

  {
    filename: "starhub_3",
    date: "20 May 2019",
    accuracy: 52,
    category: "Operations",
    status: "processed",
    country: "Hong Kong"
  },

  {
    filename: "starhub_4",
    date: "14 Jul 2019",
    accuracy: 71,
    category: "Human Resources",
    status: "reviewed",
    country: "Japan"
  }
];

class Invoices extends React.Component {
  state = {
    filteredInfo: null,
    sortedInfo: null
  };

  handleChange = (pagination, filters, sorter) => {
    console.log("Various parameters", pagination, filters, sorter);
    this.setState({
      filteredInfo: filters,
      sortedInfo: sorter
    });
  };

  clearFilters = () => {
    this.setState({ filteredInfo: null });
  };

  clearAll = () => {
    this.setState({
      filteredInfo: null,
      sortedInfo: null
    });
  };

  formatAccCol = percent => {
    const r =
      percent < 50 ? 255 : Math.floor(255 - ((percent * 2 - 100) * 255) / 100);
    const g = percent > 50 ? 255 : Math.floor((percent * 2 * 255) / 100);
    return "rgb(" + r + "," + g + ",0)";
  };

  render() {
    let { sortedInfo, filteredInfo } = this.state;
    sortedInfo = sortedInfo || {};
    filteredInfo = filteredInfo || {};

    const columns = [
      {
        title: "File Name",
        dataIndex: "filename",
        key: "filename",
        sorter: (a, b) => a.filename > b.filename,
        sortOrder: sortedInfo.columnKey === "filename" && sortedInfo.order,
        ellipsis: true
      },
      {
        title: "Date",
        dataIndex: "date",
        key: "date",
        filters: dates.map(date => ({ text: date, value: date })),
        filteredValue: filteredInfo.date || null,
        onFilter: (value, record) => record.date.includes(value),
        sorter: (a, b) => a.date.length - b.date.length,
        sortOrder: sortedInfo.columnKey === "date" && sortedInfo.order,
        ellipsis: true
      },
      {
        title: "Category",
        dataIndex: "category",
        key: "category",
        filters: categories.map(cat => ({ text: cat, value: cat })),
        filteredValue: filteredInfo.category || null,
        onFilter: (value, record) => record.category.includes(value),
        sorter: (a, b) => a.category.length - b.category.length,
        sortOrder: sortedInfo.columnKey === "category" && sortedInfo.order,
        ellipsis: true
      },
      {
        title: "Country",
        dataIndex: "country",
        key: "country",
        filters: [
          { text: "Singapore", value: "Singapore" },
          { text: "Hong Kong", value: "Hong Kong" },
          { text: "Japan", value: "Japan" }
        ],
        filteredValue: filteredInfo.country || null,
        onFilter: (value, record) => record.country.includes(value),
        sorter: (a, b) => a.country.length - b.country.length,
        sortOrder: sortedInfo.columnKey === "country" && sortedInfo.order,
        ellipsis: true
      },
      {
        title: "Status",
        dataIndex: "status",
        key: "status",
        filters: Object.keys(statusColours).map(k => ({ text: k, value: k })),
        filteredValue: filteredInfo.status || null,
        onFilter: (value, record) => record.status.includes(value),
        sortOrder: sortedInfo.columnKey === "status" && sortedInfo.order,
        ellipsis: true,
        render: tag => (
          <Tag color={statusColours[tag]} key={tag}>
            {tag}
          </Tag>
        )
      },
      {
        title: "Accuracy",
        dataIndex: "accuracy",
        key: "accuracy",
        filters: [
          { text: "High", value: [100, 70] },
          { text: "Mid", value: [70, 30] },
          { text: "Low", value: [30, 0] }
        ],
        filteredValue: filteredInfo.accuracy || null,
        onFilter: (value, record) =>
          record.accuracy > value[1] && record.accuracy <= value[0],
        sorter: (a, b) => a.accuracy - b.accuracy,
        sortOrder: sortedInfo.columnKey === "accuracy" && sortedInfo.order,
        ellipsis: true,
        render: acc => (
          <Tag color={this.formatAccCol(acc)} key={acc}>
            {acc}%
          </Tag>
        )
      },
      {
        title: "Actions",
        dataIndex: "actions",
        key: "actions",
        render: (text, record) => (
          <span>
            <a>Open File</a>
            <Divider type="vertical" />
            <a>Run Classifier</a>
          </span>
        )
      }
    ];
    return (
      <div className="card">
        <div>
          <div className="table-operations">
            <Button onClick={this.clearFilters}>Clear filters</Button>
            <Button onClick={this.clearAll}>Clear filters and sorters</Button>
          </div>
          <Table
            columns={columns}
            dataSource={data}
            onChange={this.handleChange}
          />
        </div>
      </div>
    );
  }
}

export default Invoices;
