import React, { useState, useEffect } from "react";
import { Layout, Menu } from "antd";

import Dashboard from "./Components/Dashboard";
import Accuracy from "./Components/Accuracy";
import Invoices from "./Components/Invoices";

import socketIOClient from "socket.io-client";

import "./App.css";

function App() {
  const { Header, Content, Footer } = Layout;
  const [page, setPage] = useState("HOME");
  const socket = socketIOClient("http://localhost:5000");

  const handlePageChange = e => {
    setPage(e.key);
  };

  useEffect(() => {
    socket.emit('req-home')
    socket.on("res-home", data => console.log(data));
  }, []);

  return (
    <div className="App">
      <Layout className="layout">
        <Header>
          <div className="logo">Dr-NET Document Classifier</div>
          <Menu
            theme="dark"
            mode="horizontal"
            defaultSelectedKeys={["HOME"]}
            style={{ lineHeight: "64px" }}
          >
            <Menu.Item onClick={handlePageChange} key="HOME">
              Home
            </Menu.Item>
            <Menu.Item onClick={handlePageChange} key="ACCURACY">
              Accuracy
            </Menu.Item>
            <Menu.Item onClick={handlePageChange} key="INVOICES">
              Invoices
            </Menu.Item>
            <Menu.Item onClick={handlePageChange} key="TRENDS">
              Trends
            </Menu.Item>
            <Menu.Item onClick={handlePageChange} key="DOCUMENTS">
              Documents
            </Menu.Item>
          </Menu>
        </Header>
        <Content style={{ padding: "30px", background: "#eaeaea" }}>
          {page === "HOME" && <Dashboard handlePageChange={handlePageChange} />}
          {page === "ACCURACY" && <Accuracy />}
          {page === "INVOICES" && <Invoices />}
        </Content>
        <Footer
          style={{ textAlign: "center", background: "#eaeaea" }}
          onClick={() => socket.send("messdfsdfsage")}
        >
          2019 NUS BT3101 CAPSTONE GROUP 2
        </Footer>
      </Layout>
    </div>
  );
}

export default App;
